<script>
// @ is an alias to /src
import HelloWorld from '@/components/HelloWorld.vue';
import CardComponent from '../components/CardComponent.vue';
import CardWorkshop from '../components/CardWorkshop.vue';
const API_URL_MSG = "https://app-namaste.herokuapp.com/workshop"; 
const API_URL_USER = "https://app-namaste.herokuapp.com/user";
export default {
  name: 'HomeView',
  components: {
    HelloWorld,CardComponent,CardWorkshop
  },
  data(){
return {    
  isAdmin:'',
  tableauMsg:[],
    tableauUsers:[],
            firstname : '',
        name: '',
        pseudo: '',
        email : '',
        music :'',
        firstnameMsg:'',
        nameMsg:'',
        workshop:'',
        description:'',
    
    }

  },
  methods: {
    verifyAdminData(){
      
      if(0<this.isAdmin.length<20)
      {

        localStorage.setItem("laisEvent", this.isAdmin+'/san');
        this.$router.go();

      }
      else
      {
        document.getElementById('isAdminErrorMsg').textContent = 'Ce n est pas le bon mot de passe';
        
      }
    },

       verifyUserData() {
      let lesValiditeDeChamps = true;
      for (let input of document.querySelectorAll(".formulaireIns input")) {
        if (input.getAttribute("type") != "submit") {
          if (!input.reportValidity()) {
            lesValiditeDeChamps = false;
            document.getElementById(
              input.getAttribute("id").concat("ErrorMsg")
            ).textContent = "Vous avez mal complété ce champ";
          } else {
            document.getElementById(
              input.getAttribute("id").concat("ErrorMsg")
            ).textContent = "";
          }
        }
      }
      if (lesValiditeDeChamps) {
        fetch("https://app-namaste.herokuapp.com/user", {
          method: "POST",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
          },
          body: JSON.stringify({
        firstname : this.firstname,
        name: this.name,
        pseudo: this.pseudo,
        email : this.email,
        music :[this.music],
          }),
        }).then(() => {
          this.$router.go();
        });
      }
    },

           verifyMessageData() {
      let lesValiditeDeChamps = true;
      for (let input of document.querySelectorAll(".formulaireWork input")) {
        if (input.getAttribute("type") != "submit") {
          if (!input.reportValidity()) {
            lesValiditeDeChamps = false;
            document.getElementById(
              input.getAttribute("id").concat("ErrorMsg")
            ).textContent = "Vous avez mal complété ce champ";
          } else {
            document.getElementById(
              input.getAttribute("id").concat("ErrorMsg")
            ).textContent = "";
          }
        }
      }
      if (lesValiditeDeChamps) {
        fetch("https://app-namaste.herokuapp.com/workshop", {
          method: "POST",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
          },
          body: JSON.stringify({
        firstname : this.firstnameMsg,
        name: this.nameMsg,
        workshop: this.workshop,
        description : this.description,
          }),
        }).then(() => {
          this.$router.go();
        });
      }
    },


  },

    created() {
      if(!(localStorage.laisEvent)||(localStorage.laisEvent.length<4)){
          localStorage.setItem("laisEvent", 'i/n');
      }
      
    fetch(API_URL_USER, {
      method: "GET",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        
      },
    })
      .then((result) => {
        return result.json();
      })
      .then((tableauUsers) => {
        console.log(tableauUsers);
        this.tableauUsers = tableauUsers;
      });

          fetch(API_URL_MSG, {
      method: "GET",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        
      },
    })
      .then((result) => {
        return result.json();
      })
      .then((tableauMsg) => {
        console.log(tableauMsg);
        this.tableauMsg = tableauMsg;
      });

      
  },

}

</script>
