<template>

    <div class="card m-2" style="width: 18rem;">
  <div class="card-body">
    <h5 class="card-title">{{wksData.workshop}}</h5>
    <h6 class="card-subtitle mb-2 text-muted">Emis par {{wksData.name}} {{wksData.firstname}}</h6>
    <p class="card-text">{{wksData.description}}</p>
    <button v-if="accessLais=='namaste'" class="btn btn-danger" @click="deleteWks(wksData._id)" >Supprimer</button>
  </div>
</div>

</template>
<script>
export default {
  name: "CardWorkshop",
  props: ["wks"],
  data(){
      
      return{
          wksData:this.wks,
          accessLais:localStorage.laisEvent.split('/')[1],
      }
  },
  methods: {
            deleteWks(id){
                alert(id);
                  fetch("https://app-namaste.herokuapp.com/workshop/"+id, {
          method: "DELETE",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
          },
        }).then(() => {
          this.$router.go();
        });

      },
      
  },

}
</script>