<template>
<div class="container">
        <div class="row border border-primary">
        <div class="col-sm-2">{{userData.name}} {{userData.firstname}}</div>
        <div class="col-sm-2">Musique que j'aime</div>
        <div class="col-sm-2">{{userData.music}}</div>
        <div class="col-sm-2">
            <span v-if="userData.isValidPayment"><button class="btn btn-success  m-2">A payé et validé</button> </span>
           
        </div>
        <div class="col-sm-2"> <span v-if="accessLais=='namaste'"> <button class="btn btn-success  m-2" @click="validPayment(userData._id)">Valider ou Invalider le paiement</button> </span></div>
        <div class="col-sm-2"> <span v-if="accessLais=='namaste'"> <button class="btn btn-danger  m-2" @click="deleteUser(userData._id)">Supprimer l'utilisateur</button> </span></div>
        

        
    </div>
</div>
</template>
<script>
export default {
  name: "CardComponent",
  props: ["user"],
  data(){
      console.log('boom');
      console.log(this.user);
      return{
          userData:this.user,
          accessLais:localStorage.laisEvent.split('/')[1],
      }
  },
  methods: {
      validPayment(id){
                  fetch("https://app-namaste.herokuapp.com/user/"+id, {
          method: "PUT",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
          },
          body: JSON.stringify({
        password : localStorage.laisEvent.split('/')[0],
          }),
        }).then(() => {
          this.$router.go();
        });

      },

            deleteUser(id){
                  fetch("https://app-namaste.herokuapp.com/user/"+id, {
          method: "DELETE",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
          },
          body: JSON.stringify({
        password : localStorage.laisEvent.split('/')[0],
          }),
        }).then(() => {
          this.$router.go();
        });

      },
      
  },

}
</script>